.container {
  padding: 20px;
  text-align: center;
}

.list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  list-style: none;
  padding: 0;
  margin: 0;
}

.list-item {
  background-color: #f0f0f0;
  border-radius: 8px;
  overflow: hidden;
  width: 200px;
  box-shadow: 0 4px 8px rgba(0,0,0,0.1);
  text-align: center;
}

.image {
  width: 100%;
  height: auto;
}

h2 {
  font-size: 1.2em;
  margin: 10px 0;
}